import React from 'react'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../layout'
import {
  Icon,
  Link,
  Picture,
  Text,
  Title
} from '../../../../atoms'
import { BackboneAisles } from '../BackBoneAisles'
import styles from './DropDownAisles.module.css'
import { AislesData } from '..'
import { getGlobalStyle } from '../../../../../helpers'
import { WrapperCategory } from './helpers/WrapperCategory'
import {
  classNameCategories,
  customClassCategory,
  customClassRowWrapperFunc,
  customFontSizeTitle,
  customFontSizeTitleChild,
  customHeightContent,
  customPadding,
  customScrollContent,
  customScrollContentStatic,
  customWidthAisles,
  customWidthContent,
  rowCustomTopContent,
  statusAisles
} from './helpers/customStyles'

export interface AislesDesktopProps {
  customMinHeight: string;
  customWidthColumn: string;
  customWidthContainer: string;
  data: AislesData[];
  isHover: boolean;
  isVisableBanner: boolean;
  isVisibleAislesAlvi?: boolean;
  isVisibleDropDownAisles?: boolean;
  openAisles?: boolean;
  statusAisles?: 'open' | 'close';
  subcategory: AislesData;
  urlBanner: string;
  linkWrapper?: React.FunctionComponent;
  hoverCategory?: (category?: AislesData) => void;
  onClickCategory: () => void;
  getCategoryUrl: (category?: AislesData) => string;
}

export const AislesDesktop = ({
  customMinHeight,
  customWidthColumn,
  customWidthContainer,
  data,
  isHover,
  isVisableBanner,
  isVisibleAislesAlvi = false,
  isVisibleDropDownAisles,
  openAisles,
  subcategory,
  linkWrapper,
  urlBanner,
  getCategoryUrl = () => { return '' },
  hoverCategory = () => { return null },
  onClickCategory = () => { return '' }
}: AislesDesktopProps): React.ReactElement => {
  const isOpenAisles = openAisles === true
  const statusAislesLateral = statusAisles(isVisibleDropDownAisles)
  const customClassNameAisles = isVisibleAislesAlvi ? statusAislesLateral : styles.aislesContainer
  const colorIconCategory = getGlobalStyle(isVisibleAislesAlvi ? '--color-icons-white' : '--color-icons-gray')
  const paddingSubCategory = isVisibleAislesAlvi ? '0px' : '28px 16px 0 0'
  const widthAisles = customWidthAisles(isOpenAisles, customWidthContainer)
  const customClassRowWrapper = customClassRowWrapperFunc(isVisibleAislesAlvi, isHover)
  const customHeight = customHeightContent(isVisibleAislesAlvi, customMinHeight)
  const rowCustomTop = rowCustomTopContent(isVisibleAislesAlvi)
  const customScroll = customScrollContent(isVisibleAislesAlvi)
  const customScrollStatic = customScrollContentStatic(isVisibleAislesAlvi)
  const customWidth = customWidthContent(isVisibleAislesAlvi, customWidthColumn)
  const resultClassCategories = classNameCategories(isVisibleAislesAlvi)
  const customFontSizeCategories = customFontSizeTitle(isVisibleAislesAlvi)
  const customFontSizeCategoriesChild = customFontSizeTitleChild(isVisibleAislesAlvi)
  const classCategories = customClassCategory(isVisibleAislesAlvi)
  const padding = customPadding(isVisibleAislesAlvi)

  return (
    <Container position='relative'>
      <Column
        backgroundColor='transparent'
        borderRadius='0 0 8px 8px'
        customClassName={customClassNameAisles}
        customWidth={widthAisles}
        padding='28px 0 0'
        position={isVisibleDropDownAisles ? 'fixed' : 'absolute'}
      >
        {!isVisibleAislesAlvi && <Container customClassName={styles.arrowBox} position='relative' />}
        <Row
          backgroundColor={getGlobalStyle('--color-base-white')}
          customClassName={customClassRowWrapper}
          customWidth={isVisibleAislesAlvi && 'max-content'}
          left={'-150'}
          minHeight={customHeight}
          position='absolute'
          top={rowCustomTop}
        >
          <Column
            backgroundColor={getGlobalStyle('--color-neutral-gray-white')}
            customClassName={customScrollStatic}
            customHeight='90vh'
            customWidth={customWidth}
            overflow='auto'
            padding={padding}
          >
            {data?.map(category => {
              return (
                <Row
                  alignItems='center'
                  key={category?.id}
                  onClick={onClickCategory}
                >
                  <Link
                    customWidth='100'
                    href={getCategoryUrl(category)}

                    onMouseEnter={() => {
                      hoverCategory(category)
                    }}
                    underline='none'
                    wrapper={linkWrapper}
                  >
                    <Row
                      alignItems='center'
                      clickable='pointer'
                      customClassName={resultClassCategories}
                      customWidth='100'
                      justifyContent='between'
                      padding='7px 6px 7px 28px'
                    >
                      <Text
                        clickable='pointer'
                        fontSize={isVisibleAislesAlvi ? 'base' : 'md'}
                        fontWeight='semibold'
                        textAlign='left'
                      >
                        {category?.name}
                      </Text>
                      {
                        (isHover && subcategory.id == category.id) &&
                      <Icon
                        clickable='pointer'
                        color={colorIconCategory}
                        name='ArrowRight'
                        sizes='xs'
                      />
                      }
                    </Row>
                  </Link>
                </Row>
              )
            })}
            {(!data || data.length === 0) && <BackboneAisles />}
          </Column>
          {isHover && (
            <Column
              customClassName={customScroll}
              justifyContent='between'
              maxHeight='90vh'
              minHeight={customMinHeight}
            >
              <Column padding='42px 0px 92px 38px'>
                <Row alignItems='center' onClick={onClickCategory}>
                  <Link
                    href={getCategoryUrl(subcategory)}
                    underline='none'
                    wrapper={linkWrapper}
                  >
                    <Title
                      color={isVisibleAislesAlvi ? getGlobalStyle('--color-alvi-primary-blue3') : ''}
                      customFontSize={isVisibleAislesAlvi ? customFontSizeCategories : ''}
                      fontWeight='bold'
                      headingLevel='h3'
                      text={subcategory.name}
                    />
                  </Link>
                  <Spacer.Vertical size={16} />
                  <Link
                    color={isVisibleAislesAlvi ? getGlobalStyle('--color-text-gray-light') : ''}
                    customClassName={classCategories}
                    fontSize={isVisibleAislesAlvi ? 'base' : 'md'}
                    href={getCategoryUrl(subcategory)}
                    wrapper={linkWrapper}
                  >
                    Ver todo
                  </Link>
                </Row>
                {isVisibleAislesAlvi && <Spacer.Horizontal size={32} />}
                <WrapperCategory isVisibleAislesAlvi={isVisibleAislesAlvi}>
                  {subcategory?.children?.map(list => {
                    return (
                      <Column
                        customWidth={isVisibleAislesAlvi ? '100' : '33'}
                        key={list.id}
                        padding={paddingSubCategory}
                      >
                        <Row onClick={onClickCategory}>
                          <Link
                            href={getCategoryUrl(list)}
                            underline='none'
                            wrapper={linkWrapper}
                          >
                            <Title
                              customClassName={isVisibleAislesAlvi ? styles.subCategoryAlvi : ''}
                              customFontSize={customFontSizeCategoriesChild}
                              fontWeight='semibold'
                              headingLevel='h6'
                              text={list?.name}
                            />
                          </Link>
                        </Row>
                        <Spacer.Horizontal size={8} />
                        {list?.children?.map(sublist => {
                          return (
                            <React.Fragment key={sublist?.id}>
                              <Spacer.Horizontal size={8} />
                              <Row onClick={onClickCategory}>
                                <Link
                                  customClassName={styles.linkCategory}
                                  fontSize='md'
                                  href={getCategoryUrl(sublist)}
                                  underline='none'
                                  wrapper={linkWrapper}
                                >
                                  {sublist?.name}
                                </Link>
                              </Row>
                            </React.Fragment>
                          )
                        })}
                        <Spacer.Horizontal size={8} />
                        {!isVisibleAislesAlvi && <Row onClick={onClickCategory}>
                          <Link
                            customClassName={styles.linkAllCategory}
                            fontSize='md'
                            href={getCategoryUrl(list)}
                            wrapper={linkWrapper}
                          >
                            Ver todo
                          </Link>
                        </Row>}
                      </Column>
                    )
                  })}
                </WrapperCategory>
              </Column>
              {isVisableBanner &&
                <Column
                  backgroundColor={getGlobalStyle('--color-neutral-gray-dark')}
                  customHeight='9.5rem'
                  customWidth='100%'
                >
                  <Picture
                    alt={subcategory.name}
                    border='none'
                    borderRadius='none'
                    height='100%'
                    src={urlBanner}
                    width='100%'
                  />
                </Column>
              }
            </Column>
          )}
        </Row>
      </Column >
    </Container >
  )
}
