import React, { useEffect } from 'react'
import { Icons } from '../../../../assets/SvgComponents/SvgIcons'
import { Icon, Text } from '../../../atoms'
import { BigScreen, SmallScreen } from '../../../../helpers'
import styles from './DropDownAisles/DropDownAisles.module.css'

import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../layout'
import {
  DropDownAisles,
  DropDownAislesProps
} from './DropDownAisles/DropDownAisles'
import { getGlobalStyle } from '../../../../helpers'

export interface AislesData {
  children?: AislesData[];
  image?: string;
  img?: string;
  fq?: string;
  hasChildren: boolean;
  id: number;
  MetaTagDescription?: string;
  name: string;
  url: string;
}

export interface AislesProps {
  dropDownAislesProps: DropDownAislesProps;
  iconAisles?: keyof (typeof Icons);
  iconSizeCategoriesMobile?: number;
  isVisibleAislesAlvi?: boolean;
  isVisibleAislesIcon?: boolean;
  isVisibleAislesIconBurger?: boolean;
  isVisibleDropDownAisles?: boolean;
  isVisibleImageAlvi?: boolean;
  site?: 'unimarc' | 'alvi';
  sizeLogo?: number;
  status?: string;
  statusAisles?: 'open' | 'close';
  title?: string;
  titleMobile?: string;
  onClickAisles?: () => void;
}

export const Aisles = ({
  dropDownAislesProps,
  iconAisles = 'Aisles',
  iconSizeCategoriesMobile,
  isVisibleAislesIcon = true,
  isVisibleAislesIconBurger = false,
  isVisibleDropDownAisles = false,
  site = 'unimarc',
  statusAisles,
  title = 'Pasillos',
  titleMobile = 'Nuestros pasillos',
  onClickAisles
}: AislesProps): React.ReactElement<AislesProps> => {
  const unimarcSite = site === 'unimarc'
  useEffect(() => {
    document.body.style['overflow-y'] = isVisibleDropDownAisles ? 'hidden' : 'scroll'
  }, [isVisibleDropDownAisles])
  const openAisles = statusAisles === 'open'
  const { isVisibleAislesAlvi } = dropDownAislesProps || {}
  const sizeCategoriesIcon = isVisibleAislesAlvi ? iconSizeCategoriesMobile : 22
  const fontWeight = unimarcSite ? 'regular' : 'semibold'

  return (
    <>
      <BigScreen>
        <Column customWidth='max-content'>
          <Row alignItems='center' onClick={onClickAisles} >
            <Column customWidth='max-content'>
              {isVisibleAislesIconBurger &&
                <Container
                  clickable='pointer'
                  customClassName={styles.BurgerMenu__container}
                  customHeight='16px'
                  margin='0 0 10px 0'
                  minHeight='16px'
                  width='21px'
                >
                  <Text customClassName={openAisles && styles.open} type='span'>
                  </Text>
                  <Text customClassName={openAisles && styles.open} type='span'>
                  </Text>
                  <Text customClassName={openAisles && styles.open} type='span'>
                  </Text>
                </Container>
              }
              {isVisibleAislesIcon &&
                <Icon
                  clickable='pointer'
                  color={getGlobalStyle('--color-icons-white')}
                  customSize={22}
                  name={iconAisles}
                />
              }
            </Column>
            <Spacer.Vertical size={8} />
            <Column customWidth='max-content'>
              <Text
                clickable='pointer'
                color='white'
                fontSize='base'
                fontWeight={fontWeight}
                underline='hover'
              >
                {title}
              </Text>
            </Column>
          </Row>
          {(isVisibleDropDownAisles && !isVisibleAislesAlvi) && <DropDownAisles {...dropDownAislesProps} />}
          {isVisibleAislesAlvi &&
            <DropDownAisles
              {...dropDownAislesProps}
              isVisibleDropDownAisles={isVisibleDropDownAisles}
              openAisles={openAisles}
              statusAisles={statusAisles}
              titleMobile={titleMobile}
            />
          }
        </Column>
      </BigScreen>

      <SmallScreen>
        <>
          <Column customWidth='20'>
            <Icon
              clickable='pointer'
              color={getGlobalStyle('--color-icons-white')}
              customSize={sizeCategoriesIcon}
              name={iconAisles}
              onClick={onClickAisles}
              site={site}
            />
          </Column>
          {isVisibleDropDownAisles && <DropDownAisles {...{ ...dropDownAislesProps, onClickAisles, site, titleMobile }} />}
        </>
      </SmallScreen>
    </>
  )
}