import {
  Icon,
  Text,
  Tooltip
} from '../../../../atoms'
import { Icons } from '../../../../../assets/SvgComponents/SvgIcons'
import { BigScreen, SmallScreen } from '../../../../../helpers/mediaQueries'
import { getGlobalStyle } from '../../../../../helpers'
import { Column, Row } from '../../../../layout'
import styles from './Coupon.module.css'

export interface CouponProps {
  couponIcon?: keyof (typeof Icons);
  couponTooltipText?: string;
  showCouponTooltip?: boolean;
  site?: 'unimarc' | 'alvi';
  text?: string;
  onClickCoupon?: () => void;
}

export const Coupon = ({
  couponIcon = 'Coupon',
  couponTooltipText = 'Nuevo',
  showCouponTooltip = false,
  site = 'unimarc',
  text = 'Cupones',
  onClickCoupon
}: CouponProps): React.ReactElement<CouponProps> => {
  const unimarcSite = site === 'unimarc'
  const customSizeIcon = unimarcSite ? 26 : 28
  const customSizeIconMovil = unimarcSite ? 26 : 24
  
  return (
    <>
      <BigScreen>
        <Column 
          className={styles.couponIcon}
          clickable='pointer'
          customWidth='max-content'
          onClick={onClickCoupon}
        >
          {showCouponTooltip && 
            <Row>
              <Tooltip 
                arrow={true}
                background={getGlobalStyle('--color-feedback-warning-light')}
                bottom='8px'
                boxShadow='0px 0px'
                colorArrow={'warning-light'}
                colorText='white'
                padding='2px 2px'
                position='relative'
                sizeArrow='xs'
                text={couponTooltipText}
                textSize='xs'
                textWeight='bold'
                type='bottom'
              />
            </Row>
          }
          <Row justifyContent='center'>
            <Icon
              clickable='pointer'
              color={getGlobalStyle('--color-icons-white')}
              customSize={customSizeIcon}
              name={couponIcon}
            />
          </Row>
          <Row justifyContent='center'>
            <Text
              clickable='pointer'
              color='white'
              fontSize='xs'
              fontWeight='semibold'
              textAlign='center'
            >
              {text}
            </Text>
          </Row>
        </Column>
      </BigScreen>

      <SmallScreen>
        <Column 
          className={styles.couponIcon} 
          customWidth='max-content'
          onClick={onClickCoupon}
        >
          {showCouponTooltip && 
            <Row>
              <Tooltip 
                arrow={true}
                background={getGlobalStyle('--color-feedback-warning-light')}
                bottom='4px'
                boxShadow='0px 0px'
                colorArrow={'warning-light'}
                colorText='white'
                left='0'
                padding='1px 4px'
                position='relative'
                sizeArrow='xs'
                text={couponTooltipText}
                textSize='xs'
                textWeight='bold'
                type='bottom'
              />
            </Row>
          }
          <Row justifyContent='center'>
            <Icon
              clickable='pointer'
              color={getGlobalStyle('--color-icons-white')}
              customSize={customSizeIconMovil}
              name={couponIcon}
            />
          </Row>
          <Row justifyContent='center'>
            <Text
              clickable='pointer'
              color='white'
              fontSize='xs'
              fontWeight='bold'
              textAlign='center'
            >
              {text}
            </Text>
          </Row>
        </Column>
      </SmallScreen>
    </>
  )
}