import { Column, Row } from '../../../../../layout'

interface IWrapperCategory {
  children: React.ReactNode,
  isVisibleAislesAlvi?: boolean,
}
export const WrapperCategory = ({ children, isVisibleAislesAlvi }: IWrapperCategory) => {
  return (
    <>
      {isVisibleAislesAlvi
        ?
        <Column>{children}</Column>
        :
        <Row isWrap>
          {children}
        </Row>
      }
    </>
  )
}

