import React, { useState } from 'react'
import { AislesData } from '..'
import { getGlobalStyle } from '../../../../../helpers'
import {
  Backbone,
  Icon,
  Link,
  Picture,
  Text
} from '../../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../../layout'
import styles from './DropDownAisles.module.css'

export interface AislesMobileProps {
  data?: AislesData[];
  title?: string;
  isVisibleImageAlvi?: boolean;
  site?: 'unimarc' | 'alvi';
  linkWrapper?: React.FunctionComponent;
  onClickAisles?: () => void;
  onClickCategory: () => void;
  getCategoryUrl?: (category?: AislesData) => string;
}

export const AislesMobile = ({
  title = 'Nuestros pasillos',
  data,
  site,
  isVisibleImageAlvi,
  onClickAisles,
  onClickCategory,
  linkWrapper,
  getCategoryUrl = () => { return '' }
}: AislesMobileProps): React.ReactElement => {
  const unimarcSite: boolean = site === 'unimarc'
  const alviSite: boolean = site === 'alvi'
  const [isErrorImg, setIsErrorImg] = useState<boolean>(false)
  const colorTitle: 'gray' | 'neutral-black' = unimarcSite ? 'gray' : 'neutral-black'
  const customClassNameContent: string = unimarcSite ? '' : styles.headerContainerAlvi
  const customHeightContent: string = unimarcSite ? 'initial' : '81px'
  const marginListCategories: string = unimarcSite ? '62px 0px 0px' : '81px 0 0 0'
  const paddingColumn = `0 0 0 ${unimarcSite ? '76px' : '50px'}`
  const paddingContent: string = unimarcSite ? '20px 12px 0px' : '25px'
  const paddingListCategories: string = unimarcSite ? '0px 12px 90px 12px' : 'initial'
  const sizeTitle: 'xl' | '2xl' = unimarcSite ? 'xl' : '2xl'

  return (
    <Container position='fixed' zIndex={getGlobalStyle('--z-index-5')}>
      <Column
        backgroundColor={getGlobalStyle('--color-base-white')}
        borderRadius='0 0 8px 8px'
        customHeight='100vh'
        overflow='scroll'
        padding='0'
        position='fixed'
      >
        <Container
          alignItems='center'
          customClassName={customClassNameContent}
          customHeight={customHeightContent}
          isWrap
          minHeight={customHeightContent}
          padding={paddingContent}
          position='absolute'
        >
          {unimarcSite &&
            <Row alignItems='center' justifyContent='end'>
              <Icon
                clickable='pointer'
                color={getGlobalStyle('--color-icons-black')}
                customSize={17}
                name='Close'
                onClick={onClickAisles}
              />
            </Row>
          }
          <Row>
            <Spacer.Vertical size={16} />
            <Column
              alignItems='center'
              customClassName={styles.aislesMobileTitle}
              customWidth='33'
              justifyContent='center'
              padding={paddingColumn}
            >
              <Text
                clickable='pointer'
                color={colorTitle}
                fontSize={sizeTitle}
                fontWeight='semibold'
                underline='hover'
              >
                {title}
              </Text>
            </Column>
            {alviSite &&
              <Row alignItems='center' justifyContent='end'>
                <Icon
                  clickable='pointer'
                  color={getGlobalStyle('--color-icons-black')}
                  customSize={20.34}
                  name='Close'
                  onClick={onClickAisles}
                />
              </Row>
            }
          </Row>
        </Container>

        <Container
          isWrap
          margin={marginListCategories}
          padding={paddingListCategories}
        >
          {data && data.map((category, index) => {
            const image = isVisibleImageAlvi ? category?.image : category?.img
            return (
              <Column
                alignItems='center'
                customWidth='33'
                justifyContent='center'
                key={`${category.id}_${index}`}
                margin='32px 0px 0px 0px'
                onClick={onClickCategory}
              >
                <Column
                  alignItems='center'
                  backgroundColor='#F1F1F1'
                  borderRadius='50%'
                  justifyContent='center'
                  maxWidth='100px'
                  minHeight='100px'
                >
                  <Link
                    href={getCategoryUrl(category)}
                    underline='none'
                    wrapper={linkWrapper}
                  >
                    {isErrorImg || !image ?
                      <Backbone
                        borderRadius='50%'
                        height={100}
                        width={100}
                      />
                      :
                      <Picture
                        alt={category?.name}
                        borderRadius='50%'
                        height='100%'
                        onError={() => {
                          setIsErrorImg(true)
                        }}
                        src={image}
                        width='100%'
                      />
                    }
                  </Link>
                </Column>
                <Link
                  href={getCategoryUrl(category)}
                  underline='none'
                  wrapper={linkWrapper}
                >
                  <Row justifyContent='center' padding='9px 12px 0'>
                    <Text
                      clickable='pointer'
                      color='black'
                      fontSize='md'
                      textAlign='center'
                      truncate={20}
                    >
                      {category?.name}
                    </Text>
                  </Row>
                </Link>
              </Column>
            )
          })}
        </Container>
      </Column >
    </Container >
  )
}