import React from 'react'
import { Icon, Text } from '../../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../layout'
import { Icons } from '../../../../assets/SvgComponents/SvgIcons'
import { DropDownMyProfile, DropDownMyProfileProps } from './DropDownMyProfile'
import { Site } from '../../../../shared/interfaces/Site'
import { BigScreen, SmallScreen } from '../../../../helpers/mediaQueries'
import { getGlobalStyle } from '../../../../helpers'
import { ShabitName } from '../../BanefitsAlvi/BenefitsAlvi'

export interface MyProfileProps {
  customBannerUrl?: string
  icon?: keyof (typeof Icons);
  isVisibleMyProfile?: boolean;
  isLoggedIn?: boolean;
  isVisibleModalLogout?: boolean;
  userFirstName?: string;
  site?: Site;
  userShabitName?: ShabitName
  onClickMyProfile?: () => void;
  onClickLogout?: () => void;
  onClickGoToMyCoupons?: () => void;
  onClickGoToMyLists?: () => void;
  onClickGoToMyOrders?: () => void;
  onClickCloseMyProfile?: () => void;
  onClickLogoutMobile?: () => void;
  onClickCloseLogoutMobile?: () => void;
  onClickLogin?: () => void;
  onClickLogo?: () => void;
  onClickMyProfileBanner?: () => void;
}

export const MyProfile = ({
  customBannerUrl,
  icon = 'User',
  isLoggedIn,
  isVisibleModalLogout = false,
  isVisibleMyProfile = false,
  site = 'unimarc',
  userFirstName,
  userShabitName,
  onClickCloseLogoutMobile,
  onClickCloseMyProfile,
  onClickGoToMyCoupons,
  onClickGoToMyLists,
  onClickGoToMyOrders,
  onClickLogin,
  onClickLogout,
  onClickLogoutMobile,
  onClickMyProfile,
  onClickMyProfileBanner
}: MyProfileProps): React.ReactElement<MyProfileProps> => {
  const unimarcSite = site === 'unimarc'
  const customSize = unimarcSite ? 26 : 24
  const sizeLogin = isLoggedIn ? 'base' : 'md'
  const customSizeLogin = unimarcSite ? 'base' : sizeLogin
  const nameFontWeight = unimarcSite ? 'regular' : 'semibold'
  const sizeIconDropdown = unimarcSite ? 14 : 11
  const customWidthContainer = unimarcSite ? 'max-content' : '100'
  const justifyContainer = unimarcSite ? 'start' : 'center'

  const dropdownMyProfileProps : DropDownMyProfileProps = {
    customBannerUrl,
    site,
    userFirstName,
    userShabitName,
    onClickCloseLogoutMobile,
    onClickCloseMyProfile,
    onClickGoToMyCoupons,
    onClickGoToMyLists,
    onClickGoToMyOrders,
    onClickLogout,
    onClickLogoutMobile,
    onClickBanner: onClickMyProfileBanner
  }

  return (
    <>
      <BigScreen>
        <Container customWidth='max-content'>
          <Row
            alignItems='center'
            clickable='pointer'
            justifyContent='end'
          >
            <Container>
              <Icon
                clickable='pointer'
                color={getGlobalStyle('--color-icons-white')}
                customSize={customSize}
                name={icon}
                onClick={isLoggedIn ? onClickMyProfile : onClickLogin}
                site={site}
              />
            </Container>
            <Spacer.Vertical size={8} />

            {isLoggedIn &&
              <Column position='relative'>
                <Row
                  alignItems='center'
                  customWidth='max-content'
                  onClick={onClickMyProfile}
                >
                  <Text
                    clickable='pointer'
                    color='white'
                    fontSize={customSizeLogin}
                    fontWeight={nameFontWeight}
                    underline='hover'
                  >
                    {userFirstName}
                  </Text>
                  <Spacer.Vertical size={8} />
                  <Icon
                    clickable='pointer'
                    color={getGlobalStyle('--color-icons-white')}
                    customSize={sizeIconDropdown}
                    name='DropDownClose'
                    site={site}
                  />
                </Row>
                {isVisibleMyProfile &&
                  <DropDownMyProfile {...dropdownMyProfileProps} />
                }
              </Column>
            }

            {!isLoggedIn &&
              <Row
                alignItems='center'
                maxWidth='max-content'
                onClick={onClickLogin}
              >
                <Text
                  clickable='pointer'
                  color='white'
                  fontSize={customSizeLogin}
                  underline='hover'
                >
                  Ingresar
                </Text>
                <Spacer.Vertical size={16} />
              </Row>
            }
          </Row>
        </Container>
      </BigScreen>

      <SmallScreen>
        <Container>
          {isLoggedIn &&
            <Column>
              <Row
                customWidth={customWidthContainer}
                justifyContent={justifyContainer}
                onClick={onClickMyProfile}
              >
                <Text
                  clickable='pointer'
                  color='white'
                  fontSize={customSizeLogin}
                  truncate={10}
                  underline='always'
                >
                  {userFirstName}
                </Text>
              </Row>
              <DropDownMyProfile
                isVisibleModalLogout={isVisibleModalLogout}
                isVisibleMyProfile={isVisibleMyProfile}
                {...dropdownMyProfileProps}
              />
            </Column>
          }
          {!isLoggedIn &&
            <Column>
              <Row customWidth='max-content' onClick={onClickLogin}>
                <Text
                  clickable='pointer'
                  color='white'
                  fontSize={customSizeLogin}
                  underline='always'
                >
                  Ingresar
                </Text>
              </Row>
            </Column>
          }
        </Container>
      </SmallScreen>
    </>
  )
}
