import React, { useState } from 'react'
import { AislesData } from '..'
import { AislesDesktop } from './AislesDesktop'
import { AislesMobile } from './AislesMobile'
import { BigScreen, SmallScreen } from '../../../../../helpers'

export interface DropDownAislesProps {
  data: AislesData[];
  isVisableBanner?: boolean;
  isVisibleAislesAlvi?: boolean;
  isVisibleDropDownAisles?: boolean;
  isVisibleImageAlvi?: boolean;
  linkWrapper?: React.FunctionComponent;
  openAisles?: boolean;
  site?: 'unimarc' | 'alvi';
  statusAisles?: 'open' | 'close';
  titleMobile?: string;
  urlBanner?: string;
  getCategoryUrl?: (category?: AislesData) => string;
  onClickAisles?: () => void;
  onClickCategory: () => void;
  onHoverCategory?: (category?: AislesData) => void;
}

export const DropDownAisles = ({
  data,
  isVisableBanner,
  isVisibleAislesAlvi,
  isVisibleDropDownAisles,
  isVisibleImageAlvi = false,
  linkWrapper,
  openAisles,
  site = 'unimarc',
  statusAisles,
  titleMobile,
  urlBanner,
  getCategoryUrl,
  onClickAisles,
  onClickCategory,
  onHoverCategory
}: DropDownAislesProps): React.ReactElement<DropDownAislesProps> => {
  const [isHover, setIsHover] = useState(false)
  const [subcategory, setSubcategory] = useState<AislesData>(null)
  const customWidthContainer = isHover ? '1200' : '330'
  const customWidthColumn = isHover ? '38' : '100'
  const customMinHeight = '640px'

  const hoverCategory = (category: AislesData) => {
    if (!isHover) setIsHover(true)
    if (JSON?.stringify(category) !== JSON?.stringify(subcategory)) {
      if (onHoverCategory) onHoverCategory(category)
      setSubcategory(category)
    }
  }

  const propsAislesDesktop = {
    customMinHeight,
    customWidthColumn,
    customWidthContainer,
    data,
    isHover,
    isVisableBanner,
    isVisibleAislesAlvi,
    isVisibleDropDownAisles,
    openAisles,
    statusAisles,
    subcategory,
    urlBanner,
    linkWrapper,
    getCategoryUrl,
    hoverCategory,
    onClickCategory
  }

  const aislesMobileProps = {
    data,
    isVisibleImageAlvi,
    site,
    title: titleMobile,
    linkWrapper,
    getCategoryUrl,
    onClickAisles,
    onClickCategory
  }

  return (
    <>
      <BigScreen>
        <AislesDesktop {...propsAislesDesktop} />
      </BigScreen >

      <SmallScreen>
        <AislesMobile {...aislesMobileProps} />
      </SmallScreen>
    </>
  )
}