import { getGlobalStyle } from '../../../../helpers'
import { Icon, Tooltip } from '../../../atoms'
import { Container } from '../../../layout'
import styles from './AccountTooltip.module.css'

export interface AccountTooltipProps {
  primaryText?: string
  secondaryText?: string
  onClose?: () => void
}

export const AccountTooltip = ({
  primaryText,
  secondaryText,
  onClose
}: AccountTooltipProps) => {
  const handleTooltipClose = () => {
    if (onClose) {
      onClose()
    }
  }

  return (
    <Tooltip
      align='start'
      append={
        <Container
          justifyContent='center'
          margin='-12px 0 0 16%'
          position='absolute'
        >
          <Icon name='Polygon' />
        </Container>
      }
      closeColor={getGlobalStyle('--color-base-black')}
      closeIcon
      containerProps={{ maxWidth: '0' }}
      customClassName={styles.tooltip}
      onClick={handleTooltipClose}
      position='absolute'
      right='calc(25% - 80px)'
      text={secondaryText}
      textSize='md'
      title={primaryText}
      titleSize='md'
      top='calc(100% + 20px)'
      type='top'
    />
  )
}
