import React from 'react'
import { Container, Row } from '../../../../layout'
import { BigScreen, SmallScreen } from '../../../../../helpers/mediaQueries'
import { Site } from '../../../../../shared/interfaces/Site'
import { MyProfileContent } from '../MyProfileContent'
import { ShabitName } from '../../../BanefitsAlvi/BenefitsAlvi'
import classNames from 'classnames'
import styles from '../myProfile.module.css'

export interface DropDownMyProfileProps {
  customBannerUrl?: string
  customClassName?: string;
  isVisibleModalLogout?: boolean;
  isVisibleMyProfile?: boolean;
  site?: Site
  userFirstName?: string
  userShabitName?: ShabitName
  onClickCloseLogoutMobile?: () => void;
  onClickCloseMyProfile?: () => void;
  onClickGoToMyCards?: () => void;
  onClickGoToMyCoupons?: () => void;
  onClickGoToMyLists?: () => void;
  onClickGoToMyOrders?: () => void;
  onClickLogout?: () => void;
  onClickLogoutMobile?: () => void;
  onClickBanner?: () => void
}

export const DropDownMyProfile = ({
  customBannerUrl,
  customClassName,
  isVisibleModalLogout,
  isVisibleMyProfile,
  site,
  userFirstName,
  userShabitName,
  onClickCloseLogoutMobile,
  onClickCloseMyProfile,
  onClickGoToMyCards,
  onClickGoToMyCoupons,
  onClickGoToMyLists,
  onClickGoToMyOrders,
  onClickLogout,
  onClickLogoutMobile,
  onClickBanner
}: DropDownMyProfileProps): React.ReactElement<DropDownMyProfileProps> => {

  const myProfileContentProps: DropDownMyProfileProps = {
    userFirstName,
    customBannerUrl,
    userShabitName,
    onClickCloseMyProfile,
    onClickGoToMyCards,
    onClickGoToMyCoupons,
    onClickGoToMyLists,
    onClickGoToMyOrders,
    onClickLogout,
    onClickLogoutMobile,
    onClickCloseLogoutMobile,
    onClickBanner,
    isVisibleMyProfile,
    isVisibleModalLogout,
    site
  }

  return (
    <>
      <BigScreen>
        <Container
          absoluteDefault='topRight'
          customClassName={classNames(styles.dropDownMyProfile, customClassName)}
          padding='50px 0 0'
          position='absolute'
        >
          <Row customClassName={styles.arrowBox} position='relative' />
          <MyProfileContent padding='12px' {...myProfileContentProps} />
        </Container>
      </BigScreen>

      <SmallScreen>
        <MyProfileContent {...myProfileContentProps} />
      </SmallScreen>
    </>
  )
}