import { getGlobalStyle } from '../../../../../../helpers'
import styles from '../DropDownAisles.module.css'

export const classNameCategories = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi ? styles.categoryRowAlvi : styles.categoryRow
}
export const customWidthContent = (isVisibleAislesAlvi: boolean, customWidthColumn: string) => {
  return isVisibleAislesAlvi ? '100' : customWidthColumn
}
export const customScrollContent = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi ? styles.scrollBarAlvi : styles.scrollBar
}
export const customHeightContent = (isVisibleAislesAlvi: boolean, customMinHeight: string): string => {
  return isVisibleAislesAlvi ? 'calc(100vh - 80px)' : customMinHeight
}
export const rowCustomTopContent = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi ? '31' : '28'
}
export const customClassRowWrapperFunc = (isVisibleAislesAlvi: boolean, isHover: boolean): string => {
  return isVisibleAislesAlvi && (isHover ? styles.rowWrapper : styles.rowWrapper_custom)
}
export const customFontSizeTitle = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi && getGlobalStyle('--font-size-3xl')
}
export const customFontSizeTitleChild = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi ? getGlobalStyle('--font-size-titles-base') : ''
}
export const customScrollContentStatic = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi ? styles.scrollBarAlviStatic : styles.scrollBar
}
export const customClassCategory = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi ? styles.linkAllCategoryAlvi : styles.linkAllCategory
}
export const statusAisles = (isVisibleDropDownAisles: boolean): string => {
  return isVisibleDropDownAisles ? styles.statusAislesOpen : styles.statusAislesClose
}
export const customWidthAisles = (isOpenAisles: boolean, customWidthContainer: string): string => {
  return isOpenAisles ? '300' : customWidthContainer
}
export const customPadding = (isVisibleAislesAlvi: boolean): string => {
  return isVisibleAislesAlvi ? '37px 0 20px' : '16px 0 20px'
}