import React from 'react'
import { Icons } from '../../../../assets/SvgComponents/SvgIcons'
import { Icon } from '../../../atoms'
import {
  BigScreen,
  SmallScreen
} from '../../../../helpers/mediaQueries'
import { Column } from '../../../layout'
import { getGlobalStyle } from '../../../../helpers'

interface LogoProps {
  logo?: keyof (typeof Icons);
  onClickLogo?: () => void;
  sizeLogo?: number;
  sizeLogoMobile?: number;
}

export const LogoHeader = ({
  logo,
  sizeLogo = 150,
  sizeLogoMobile = 107,
  onClickLogo
}: LogoProps): React.ReactElement<LogoProps> => {
  return (
    <>
      <BigScreen>
        <Column customWidth='max-content'>
          <Icon
            clickable='pointer'
            color={getGlobalStyle('--color-icons-white')}
            customSize={sizeLogo}
            name={logo}
            onClick={onClickLogo}
          />
        </Column>
      </BigScreen>

      <SmallScreen>
        <Column>
          <Icon
            clickable='pointer'
            color={getGlobalStyle('--color-icons-white')}
            customSize={sizeLogoMobile}
            name={logo}
            onClick={onClickLogo}
          />
        </Column>
      </SmallScreen>
    </>
  )
}