import { getGlobalStyle } from '../../../../helpers'
import {
  BigScreen,
  SmallScreen
} from '../../../../helpers/mediaQueries'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../../layout'
import { CartIcon, CartIconProps } from '../../../molecules'
import { Coupon, CouponProps } from './Coupon'

export type Color = 'primary' | 'secondary' | 'gray' | 'inactive' | 'black' | 'white' | 'success' | 'error' | 'warning' | 'blue'

export interface CouponCartProps extends CouponProps, CartIconProps {
  isVisibleCoupon?: boolean;
}

export const CouponCart = ({
  colorQuantity = 'primary',
  couponIcon = 'Coupon',
  isVisibleCoupon = true,
  quantity,
  quantityLoading,
  showCouponTooltip,
  site = 'unimarc',
  onClickCoupon,
  onClickCart
}: CouponCartProps): React.ReactElement<CouponCartProps> => {
  const unimarcSite = site === 'unimarc'
  const customWidthRow = unimarcSite ? '20' : '10'
  const customSizesQuantityContainer = unimarcSite ? '18px' : '22px'
  const colorLoaderQuantity = unimarcSite ? getGlobalStyle('--color-primary-red-dark') : getGlobalStyle('--color-alvi-primary-blue')
  const cartIconPaddingTopBigScreen = showCouponTooltip ? '16px' : '0px'
  const cartIconPaddingBottomSmallScreen = showCouponTooltip ? '4px' : '10px'

  return (
    <>
      <BigScreen>
        <Row
          alignItems='center'
          customWidth={customWidthRow}
          justifyContent='end'
        >
          {isVisibleCoupon &&
            <Coupon
              couponIcon={couponIcon}
              onClickCoupon={onClickCoupon}
              showCouponTooltip={showCouponTooltip}
              site={site}
            />
          }
          <Spacer.Vertical size={16} />
          <Container maxWidth='35px' padding={`${cartIconPaddingTopBigScreen} 0 12px`} >
            <CartIcon
              colorLoaderQuantity={colorLoaderQuantity}
              colorQuantity={colorQuantity}
              customSizesQuantityContainer={customSizesQuantityContainer}
              onClickCart={onClickCart}
              quantity={quantity}
              quantityLoading={quantityLoading}
              site={site}
            />
          </Container>
        </Row>
      </BigScreen>

      <SmallScreen>
        <Column>
          <Row alignItems='center' justifyContent='end'>
            {isVisibleCoupon &&
              <Coupon
                couponIcon={couponIcon}
                onClickCoupon={onClickCoupon}
                showCouponTooltip={showCouponTooltip}
                site={site}
              />
            }
            <Spacer.Vertical size={8} />
            <Container maxWidth='35px' padding={`0 0 ${cartIconPaddingBottomSmallScreen}`}>
              <CartIcon
                colorLoaderQuantity={colorLoaderQuantity}
                colorQuantity={colorQuantity}
                customSizesIcon={20}
                customSizesQuantity={'2xs'}
                customSizesQuantityContainer={'18px'}
                onClickCart={onClickCart}
                quantity={quantity}
                quantityLoading={quantityLoading}
                site={site}
              />
            </Container>
          </Row>
        </Column>
      </SmallScreen>
    </>
  )
}