import React from 'react'
import {
  Aisles,
  AislesProps,
  CouponCart,
  CouponCartProps,
  LogoHeader,
  MyProfile,
  MyProfileProps,
  StoreLocatorHeader,
  StoreLocatorHeaderProps
} from '.'
import { Icons } from '../../../assets/SvgComponents/SvgIcons'
import {
  getGlobalStyle,
  BigScreen,
  SmallScreen
} from '../../../helpers'
import { Link } from '../../atoms'
import {
  Column,
  Container,
  Row,
  Spacer
} from '../../layout'
import { Search, SearchProps } from '../Search/Search'
import {
  AccountTooltip,
  AccountTooltipProps
} from './AccountTooltip/AccountTooltip'

export interface HeaderProps
  extends StoreLocatorHeaderProps,
    MyProfileProps,
    CouponCartProps {
  accountTooltipProps?: AccountTooltipProps
  aislesProps: AislesProps
  background?: string
  isAccountTooltipOpen?: boolean
  isMobile?: boolean
  sizeLogo?: number
  sizeLogoMobile?: number
  linkWrapper?: React.FunctionComponent;
  linkOffers?: string
  logo?: keyof typeof Icons
  searchProps: SearchProps
  validationStatus?: string
  urlLogo?: string;
}

export const LayoutHeader = ({
  accountTooltipProps,
  addressSelected,
  aislesProps,
  background = getGlobalStyle('--color-primary-red-header'),
  colorQuantity = 'primary',
  couponIcon = 'Coupon',
  customBannerUrl,
  iconStoreLocator,
  isAccountTooltipOpen = false,
  isLoggedIn = false,
  isMobile = false,
  isVisibleCoupon,
  isVisibleModalLogout = false,
  isVisibleMyProfile = true,
  linkOffers = '/',
  logo = 'LogoHeader',
  quantity,
  quantityLoading,
  showCouponTooltip,
  site = 'unimarc',
  sizeLogo,
  sizeLogoMobile,
  timeTable,
  userFirstName,
  userShabitName,
  smallScreenSLVisible = true,
  searchProps: {
    backgroundGradientIconSearch = getGlobalStyle('--color-primary-red'),
    backgroundIconSearch = getGlobalStyle('--color-primary-red'),
    backgroundSearch = getGlobalStyle('--color-base-transparent'),
    borderSearch = `1px solid ${getGlobalStyle('--color-base-white')}`,
    iconArrowSearch,
    iconArrowSize,
    iconSearch = 'SearchCart',
    iconWidth = '15',
    isVisibleDropDownSearch = false,
    searchPlaceholder = '¿Qué productos necesitas hoy?',
    ...searchProps
  },
  validationStatus,
  linkWrapper,
  urlLogo,
  onClickCart,
  onClickCloseLogoutMobile,
  onClickCloseMyProfile,
  onClickCoupon,
  onClickGoToMyCoupons,
  onClickGoToMyLists,
  onClickGoToMyOrders,
  onClickLogin,
  onClickLogout,
  onClickLogoutMobile,
  onClickMyProfile,
  onClickMyProfileBanner,
  onClickStoreLocator
}: HeaderProps): React.ReactElement<HeaderProps> => {
  const couponCartProps = {
    colorQuantity,
    couponIcon,
    isLoggedIn,
    isVisibleCoupon,
    quantity,
    quantityLoading,
    showCouponTooltip,
    site,
    onClickCart,
    onClickCoupon,
    onClickLogin,
    onClickLogout
  }
  const myProfileProps : MyProfileProps = {
    customBannerUrl,
    isLoggedIn,
    isVisibleModalLogout,
    isVisibleMyProfile,
    site,
    userFirstName,
    userShabitName,
    onClickCloseLogoutMobile,
    onClickCloseMyProfile,
    onClickGoToMyCoupons,
    onClickGoToMyLists,
    onClickGoToMyOrders,
    onClickLogin,
    onClickLogout,
    onClickLogoutMobile,
    onClickMyProfile,
    onClickMyProfileBanner
  }

  const storeLocatorHeaderProps = {
    addressSelected,
    iconStoreLocator,
    isLoggedIn,
    isMobile,
    onClickStoreLocator,
    site,
    smallScreenSLVisible,
    timeTable
  }
  const siteUnimarc: boolean = site === 'unimarc'
  const fontWeight = siteUnimarc ? 'regular' : 'semibold'
  const maxHeightHeaderMovil: string = siteUnimarc ? 'initial' : '172px'
  const maxHeightSearch: string = siteUnimarc ? 'initial' : '32px'
  const maxHeightSearchMovil: string = !siteUnimarc && '35px'
  const maxWidthHeader: string = siteUnimarc ? '1440px' : '1280px'
  const maxWidthSearch: string = siteUnimarc ? '600px' : '402px'
  const maxWidthSearchMovil: string = !siteUnimarc && '345'

  return (
    <>
      <Container
        background={background}
        justifyContent='center'
        position='sticky'
        tagName='header'
        zIndex={getGlobalStyle('--z-index-10')}
      >
        <BigScreen>
          <Column
            customWidth='90'
            justifyContent='center'
            maxWidth={maxWidthHeader}
            minHeight='79px'
          >
            <Row alignItems='center' justifyContent='around'>
              {/* logo */}
              <Link
                href={urlLogo}
                label='Home'
                wrapper={linkWrapper}
              >
                <LogoHeader
                  logo={logo}
                  sizeLogo={sizeLogo}
                />
              </Link>
              <Row customWidth='max-content'>
                <Spacer.Vertical size={24} />
              </Row>

              {/* categories */}
              <Aisles {...aislesProps} site={site} />
              <Column customWidth='max-content'>
                <Spacer.Vertical size={28} />
              </Column>

              {/* offers */}
              {siteUnimarc && (
                <Column customWidth='max-content'>
                  <Link
                    color={getGlobalStyle('--color-text-white')}
                    fontSize='base'
                    fontWeight={fontWeight}
                    href={linkOffers}
                    label='Ofertas'
                    underline='hover'
                    wrapper={linkWrapper}
                  />
                </Column>
              )}

              <Column customWidth='max-content'>
                <Spacer.Vertical size={24} />
              </Column>

              <Column maxWidth={maxWidthSearch} minHeight={maxHeightSearch}>
                {/* search */}
                <Search
                  backgroundGradientIconSearch={backgroundGradientIconSearch}
                  backgroundIconSearch={backgroundIconSearch}
                  backgroundSearch={backgroundSearch}
                  borderSearch={borderSearch}
                  iconSearch={iconSearch}
                  iconWidth={iconWidth}
                  isLoggedIn={isLoggedIn}
                  isVisibleDropDownSearch={isVisibleDropDownSearch}
                  linkWrapper={linkWrapper}
                  searchPlaceholder={searchPlaceholder}
                  site={site}
                  validationStatus={validationStatus}
                  {...searchProps}
                />
              </Column>

              <Column customWidth='max-content'>
                <Spacer.Vertical size={16} />
              </Column>

              <StoreLocatorHeader {...storeLocatorHeaderProps} />

              <Row customWidth='max-content'>
                <Spacer.Vertical size={8} />
              </Row>

              <Container
                alignItems='center'
                justifyContent='center'
                position='relative'
                width='fit-content'
              >
                {/* my profile dropdown */}
                <MyProfile {...myProfileProps} />

                {isAccountTooltipOpen && (
                  <AccountTooltip {...accountTooltipProps} />
                )}
              </Container>

              <Row customWidth='max-content'>
                <Spacer.Vertical size={32} />
              </Row>

              {/* icon of coupons and minicart */}
              <CouponCart {...couponCartProps} />
            </Row>
          </Column>
        </BigScreen>

        <SmallScreen>
          <Column customWidth='90' maxHeight={maxHeightHeaderMovil}>
            <Spacer.Horizontal size={12} />
            <Column>
              <Row alignItems='center' justifyContent='between'>
                {/* categories */}
                <Aisles {...aislesProps} site={site} />
                <Spacer.Vertical size={4} />
                {/* logo */}
                <Row>
                  <Link
                    href={urlLogo}
                    label='Logo Home'
                    wrapper={linkWrapper}
                  >
                    <LogoHeader
                      logo={logo}
                      sizeLogoMobile={sizeLogoMobile}
                    />
                  </Link>
                </Row>
                <Row
                  alignItems='center'
                  justifyContent='end'
                  maxWidth='300px'
                >
                  <Container
                    alignItems='center'
                    justifyContent='center'
                    position='relative'
                    width='fit-content'
                  >
                    {/* my profile dropdown */}
                    <MyProfile {...myProfileProps} />
                    {isAccountTooltipOpen && (
                      <AccountTooltip {...accountTooltipProps} />
                    )}
                  </Container>
                  <Spacer.Vertical size={16} />

                  {/* icon of coupons and minicart */}
                  <CouponCart {...couponCartProps} />
                </Row>
              </Row>
              <Spacer.Horizontal size={8} />
              {/* search */}
              <Column
                height={maxHeightSearch}
                maxHeight={maxHeightSearchMovil}
                maxWidth={maxWidthSearchMovil}
              >
                <Search
                  backgroundGradientIconSearch={backgroundGradientIconSearch}
                  backgroundIconSearch={backgroundIconSearch}
                  backgroundSearch={backgroundSearch}
                  borderSearch={borderSearch}
                  iconArrowSearch={iconArrowSearch}
                  iconArrowSize={iconArrowSize}
                  iconSearch={iconSearch}
                  iconWidth={iconWidth}
                  isLoggedIn={isLoggedIn}
                  isMobile={isMobile}
                  isVisibleDropDownSearch={isVisibleDropDownSearch}
                  linkWrapper={linkWrapper}
                  searchPlaceholder={searchPlaceholder}
                  site={site}
                  sizeLogoMobile={sizeLogoMobile}
                  validationStatus={validationStatus}
                  {...searchProps}
                />
              </Column>
            </Column>
            <Spacer.Horizontal size={12} />
          </Column>
        </SmallScreen>
      </Container>
      <SmallScreen>
        <StoreLocatorHeader {...storeLocatorHeaderProps} />
      </SmallScreen>
    </>
  )
}

function headerPropsAreEqual(prevState, nextState) {
  return prevState.aislesProps === nextState.aislesProps
}

export const Header = React.memo(LayoutHeader, headerPropsAreEqual)
